import React, { Component } from 'react';
import { Route, Switch } from "react-router-dom";
import Dashboard from './Dashboard';
import Settings from './Settings';
import About from './About';
import NotFound from './NotFound';

/**
 * The root path for the GitHub Pages host site is not "/".
 * Prepending process.env.PUBLIC_URL is a workaround to ensure
 * that paths are well constructed.
 */
function prependPublicUrl(path) {
  return process.env.PUBLIC_URL + path;
}

function createRouteInfo(path, component, exact=false) {
  return {
    path,
    component,
    exact,
  };
}

function createRoute(routeInfo) {
  console.log(routeInfo);
  return <Route key={routeInfo.path} exact={routeInfo.exact} path={routeInfo.path} component={routeInfo.component}/>;
}

export const routePaths = {
  dashboard: prependPublicUrl('/'),
  settings:  prependPublicUrl('/settings'),
  about:     prependPublicUrl('/'),
};

const routeInfos = [
  createRouteInfo(routePaths.dashboard, Dashboard, true),
  createRouteInfo(routePaths.settings, Settings),
  createRouteInfo(routePaths.about, About),
];

export default class Routes extends Component {
  render() {
    return (
      <Switch>
        {routeInfos.map(createRoute)}
        <Route component={NotFound} />
      </Switch>
    );
  }
}



